import { Subject } from 'typings/Subject';

export default function getTeachers(subjects: Subject[]): string[] {
  let teachers: string[] = [];

  for (let { classes } of subjects) {
    for (let { sessions } of classes) {
      for (let { teacher } of sessions) {
        if (!teachers.includes(teacher)) {
          teachers.push(teacher);
        }
      }
    }
  }

  return teachers;
}
